import React, { useEffect, useRef, useState } from 'react';
import './App.css';

import ReactMarkdown from 'react-markdown';

import firebase from 'firebase/app';
import 'firebase/firestore';
import 'firebase/auth';
import 'firebase/analytics';
import 'firebase/messaging';


import { useAuthState } from 'react-firebase-hooks/auth';
import { useCollectionData } from 'react-firebase-hooks/firestore';


import UIfx from 'uifx';
import sound from './noti.wav';

if (!firebase.apps.length) {
  firebase.initializeApp({
      apiKey: "AIzaSyBpWmtGTqluiA1gC0CEFHrQcZncNRYtjbg",
      authDomain: "private-school-chat.firebaseapp.com",
      databaseURL: "https://private-school-chat.firebaseio.com",
      projectId: "private-school-chat",
      storageBucket: "private-school-chat.appspot.com",
      messagingSenderId: "113930891891",
      appId: "1:113930891891:web:88a3cb1a24e25b06017006"
  })
}else {
  firebase.app(); // if already initialized, use that one
}

const auth = firebase.auth();
const firestore = firebase.firestore();

const messaging = firebase.messaging();

var msgToken = null;

Notification.requestPermission()
.then(function() {
  console.log("Got permission to recive notifications")
  
  if ("serviceWorker" in navigator) {
    navigator.serviceWorker
      .register('../p-chat/firebase-messaging-sw.js')
      .then(function(registration) {
        console.log("Registration successful, scope is:", registration.scope);
        messaging.getToken({vapidKey: 'BJpP6JS8xn9lkBrB0M88LmpChsj46h4YjIoW6rrdgf6rpsl1nWvZ1nBGT7-qyKFmlhdguDvzzcMLXlX4SItN_9k', serviceWorkerRegistration : registration })
          .then((currentToken) => {
            if (currentToken) {
              console.log('current token for client: ', currentToken);
    
              msgToken = currentToken;
              console.log(msgToken);
            } else {
              console.log('No registration token available. Request permission to generate one.');
    
              // shows on the UI that permission is required 
            }
          }).catch((err) => {
            console.log('An error occurred while retrieving token. ', err);
            // catch error while creating client token
          });  
        })
        .catch(function(err) {
          console.log("Service worker registration failed, error:"  , err );
      }); 
    }
})
.catch(function() {
  console.log("Permission denied.")
});


var room = "_start";

var focused = true;
var messagesSinceLastOpen = 0;

const onFocus = () => {
  console.log("Tab is in focus");
  focused = true;
  messagesSinceLastOpen = 0;
  const favicon = document.getElementById("favicon")
  favicon.href = "/hbd-games/p-chat/favicon.ico";
  document.title = "Planner"
};

// User has switched away from the tab (AKA tab is hidden)
const onBlur = () => {
  console.log("Tab is blurred");
  focused = false;
};

const WindowFocusHandler = () => {
  useEffect(() => {
      window.addEventListener("focus", onFocus);
      window.addEventListener("blur", onBlur);
      // Calls onFocus when the window first loads
      onFocus();
      // Specify how to clean up after this effect:
      return () => {
          window.removeEventListener("focus", onFocus);
          window.removeEventListener("blur", onBlur);
      };
}, []);

  return <></>;
};

const noti = new UIfx(
  sound,
  {
    volume: 1, // number between 0.0 ~ 1.0
    throttleMs: 100
  }
)

function App() {

  const [user] = useAuthState(auth);

  const [stateRoom, setRoom] = useState(1);


  function RoomSwitch() { 
    var rng = Math.floor(Math.random() * 9567444) + 1; 

    console.log('Current: ' + stateRoom);
    setRoom(rng);
    console.log('New: ' + stateRoom);
    switchRoom();
  }

  return (
    <div className="App">
      <header>
        <h1>Chat</h1>
        <div className='buttoncontainer'>
          {user ? <button onClick={RoomSwitch} className="sign-out">Room</button> : null}
          <SignOut />
        </div>
      </header>

      <section>
        {user ? <ChatRoom /> : <SignIn />}
      </section>
      <WindowFocusHandler></WindowFocusHandler>
    </div>
  );
}

function RoomSelect() {
  return (
    <div id="overlay">
      <div id="overlayText"><RoomList /></div>
    </div>
  );
}

function RoomList() {
  const messagesRef = firestore.collection("rooms");
  const query = messagesRef.orderBy('createdAt');

  const [messages] = useCollectionData(query, { idField: 'id' });

  return (<>
    <div className="roomList">

      <div className='roomtex'>Close the room list by hitting the room button to apply changes.</div>

      {messages && messages.map(msg => <RoomButton key={msg.id} message={msg} />)}

    </div>
  </>)
}

function RoomButton(props) {
  const { roomName, roomSysName } = props.message;
  console.log("message");

  function SetRoom() {
    room = roomSysName;
  }

  return (<>
    <div>
      <button onClick={SetRoom}>{roomName}</button>
    </div>
  </>)
}

function switchRoom() {
  
  if (document.getElementById("overlay").style.display !== "flex") {
    document.getElementById("overlay").style.display = "flex";
  } else {
    document.getElementById("overlay").style.display = "none";
  } 
}

function SignIn() {

  const signInWithGoogle = () => {
    const provider = new firebase.auth.GoogleAuthProvider();
    auth.signInWithPopup(provider);
  }

  return (
    <>
      <button className="sign-in" onClick={signInWithGoogle}>Sign In</button>
      <div className="opening-text">If you cannot see any messages upon logging in, check your internet connection.<br/> If that does not work, chances are this service has reached it's 'quota' and will no longer function until the next day.</div>
    </>
  )

}

function SignOut() {
  
  return auth.currentUser && (
    <button className="sign-out" onClick={RemoveAuth}>Sign Out</button>
  )
}

function RemoveAuth() {
  auth.signOut();
}


function ChatRoom() {
  const dummy = useRef();
  const messagesRef = firestore.collection(room);
  const query = messagesRef.orderBy('createdAt').limitToLast(50);

  const [messages] = useCollectionData(query, { idField: 'id' });

  const [formValue, setFormValue] = useState('');

  const sendMessage = async (e) => {
    e.preventDefault();

    const { uid, photoURL } = auth.currentUser;
    var username = auth.currentUser.displayName;

    await messagesRef.add({
      text: formValue,
      createdAt: firebase.firestore.FieldValue.serverTimestamp(),
      uid,
      photoURL,
      name: username
    })

    setFormValue('');
  }

  useEffect(() => {
    dummy.current.scrollIntoView({ behavior: 'smooth' });
    noti.play();
    if (!focused) {
      messagesSinceLastOpen += 1;
      document.title = "(" + messagesSinceLastOpen + ") Planner";
      const icon = document.getElementById("favicon")
      icon.href = "/hbd-games/p-chat/favicon-unread.ico";
    }
  }, [messages])

  return (<>
    <main>
        <RoomSelect />
        {messages && messages.map(msg => <ChatMessage key={msg.id} message={msg} />)}
    </main>
    <span ref={dummy}></span>

    

    <form onSubmit={sendMessage}>

      <input value={formValue} onChange={(e) => setFormValue(e.target.value)} placeholder="Say something poggers" />

      <button type="submit" disabled={!formValue}><span role="img" aria-label='Send Message'>📤</span></button>

    </form>
  </>)
}


function ChatMessage(props) {
  const dummy = useRef();
  const { text, uid, photoURL, createdAt, name } = props.message;

  const messageClass = uid === auth.currentUser.uid ? 'sent' : 'received';

  var username;

  if (name !== undefined){
    if (name !== auth.currentUser.displayName)
    {
      username = name + " • ";
    } else {
      username = "You • "
    }
  } else {
    username = "Name missing • ";
  }
  
    

  var timestamp = 'Sending...';
  var timestampClass = '';

  if (createdAt != null) {
    timestamp = new Date(createdAt.toDate());
    var tsTime = timestamp.toTimeString();

    var tsTimeSplit = tsTime.split(':');

    var pmAm = ' ';

    if (tsTimeSplit[0] >= 13) {
      var tsInt = parseInt(tsTimeSplit[0]);
      tsInt = tsInt - 12;
      tsTimeSplit[0] = tsInt.toString();
      pmAm = 'PM';
    } else {
      pmAm = 'AM';
    }

    timestamp = `${timestamp.toDateString()}  ${tsTimeSplit[0]}:${tsTimeSplit[1]} ${pmAm}`
    
  }

  if (messageClass === 'sent'){
    timestampClass = 'tssent';
  } else {
    timestampClass = 'tsreceived';
  }

  return (<>
    <div className={`message ${messageClass}`}>
      <img alt="Profile" src={photoURL || 'https://hideyboi.github.io/hideyboidev/p-chat/missingProfilePicture.png'} />
      <div className="MessageContainer"><ReactMarkdown>{text}</ReactMarkdown></div>
    </div>
    <div className={`timestam ${timestampClass}`} margin="0px" padding="0px">{username}{timestamp}</div>
    <span ref={dummy}></span>
  </>)
}


export default App;
